.page {
  font-family: "Inter", sans-serif;
  font-weight: 700;
  font-size: 16px;
  scroll-behavior: smooth;
  
  &__body {
    margin: 0;

    &--with-menu {
      overflow: hidden;
    }
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    transform: translateX(-100%);
    transition: transform 0.3s;

    &:target {
      transform: translateX(0);
    }
  }

  &__section {
    margin-top: 120px;
  }

  &__section--title {
    font-weight: 700;
    font-size: 32px;
    margin-bottom: 48px;
    
    text-align: center;
    letter-spacing: -1.5px;
    color: $text-color;

    @include onDesktop {
      margin-bottom: 64px;
      font-size: 48px;
    }
  }
}

h1 {
  margin: 0;
}

h2 {
  margin: 0;
}

.link {
  text-decoration: none;
}

.picture:hover {
  border: 2px solid $text-color;
}
